html, body {
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Yu Gothic", YuGothic, "ヒラギノ角ゴ ProN W3", Hiragino Kaku Gothic ProN, Arial, "メイリオ", Meiryo, sans-serif;
  font-size: 15px;
  color: #333; }

i {
  color: #e3695f; }

.fa-ul {
  margin-left: 0; }
  .fa-ul > li {
    font-size: 1.2rem; }
    .fa-ul > li > div {
      position: relative;
      margin-left: 2.14286em; }

.clearfix:after {
  content: "";
  display: block;
  clear: both; }

.container {
  width: 100%;
  height: 100vh; }

main {
  width: 100%;
  height: 100%; }

/*******  side-nav  *******/
.side-nav {
  float: left;
  width: calc(20% - 1px);
  height: 100%;
  border-right: 1px solid #ddd; }
  .side-nav .profile {
    position: relative;
    margin: 0;
    padding: 20px 18px;
    background: #fbe1e1;
    font-size: 1.3rem;
    letter-spacing: 0.05em; }
    .side-nav .profile i {
      margin-right: 12px;
      font-size: 1.5rem; }
    .side-nav .profile .signout {
      position: absolute;
      right: 0;
      cursor: pointer; }
  .side-nav .category-list {
    padding: 0 5px; }
    .side-nav .category-list li {
      padding: 20px 0;
      background: #fff;
      border-radius: 5px;
      transition: background-color 0.3s;
      cursor: pointer; }
      .side-nav .category-list li.open {
        background: #ddd; }
      .side-nav .category-list li:not(:first-child) {
        margin-top: 5px; }
      .side-nav .category-list li:hover:not(.open) {
        background: #eee; }
      .side-nav .category-list li.add-category {
        color: #ccc; }

/*******  task-list  *******/
.sec-todo-list {
  float: left;
  width: 80%;
  height: 100%; }
  .sec-todo-list .list-header {
    position: relative;
    height: 200px;
    background: url(../images/bg.png) center;
    background-size: cover;
    filter: alpha(opacity=75);
    z-index: 1; }
    .sec-todo-list .list-header h1 {
      position: absolute;
      bottom: 15px;
      left: 35px;
      margin: 0;
      font-size: 2.5rem; }
      .sec-todo-list .list-header h1 input {
        color: #fff;
        background: inherit;
        border: none;
        box-shadow: none;
        outline: none; }
    .sec-todo-list .list-header > nav {
      position: absolute;
      bottom: 15px;
      right: 10px; }
      .sec-todo-list .list-header > nav > div:not(.popup-menu) {
        display: inline-block;
        padding: 10px;
        border-radius: 5px;
        background: rgba(1, 1, 1, 0.3);
        color: #fff;
        cursor: pointer;
        transition: background-color 0.3s; }
        .sec-todo-list .list-header > nav > div:not(.popup-menu):hover {
          background: rgba(1, 1, 1, 0.5); }
        .sec-todo-list .list-header > nav > div:not(.popup-menu).other {
          padding: 10px 12px; }
        .sec-todo-list .list-header > nav > div:not(.popup-menu) i {
          color: #fff; }
      .sec-todo-list .list-header > nav .popup-menu {
        display: none;
        position: absolute;
        right: 0;
        width: 200px;
        background: #fff;
        border-radius: 5px;
        box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3); }
        .sec-todo-list .list-header > nav .popup-menu.open {
          display: block; }
        .sec-todo-list .list-header > nav .popup-menu h2 {
          margin: 0;
          padding: 15px 0;
          font-size: 1.1rem;
          text-align: center;
          border-bottom: 1px solid #ddd; }
        .sec-todo-list .list-header > nav .popup-menu ul {
          margin: 0;
          padding: 0 5px; }
          .sec-todo-list .list-header > nav .popup-menu ul li {
            margin: 5px 0;
            padding: 15px 0;
            background: #fff;
            font-size: 1.1rem;
            border-radius: 5px;
            cursor: pointer;
            transition: background-color 0.3s; }
            .sec-todo-list .list-header > nav .popup-menu ul li:hover {
              background: #eee; }
            .sec-todo-list .list-header > nav .popup-menu ul li.trash {
              color: #f44336; }
              .sec-todo-list .list-header > nav .popup-menu ul li.trash i {
                color: #f44336; }
            .sec-todo-list .list-header > nav .popup-menu ul li div {
              margin-left: 2.14286em;
              padding-left: 5px; }
  .sec-todo-list .sort-data {
    display: none;
    padding: 0 15px;
    background: #e3695f;
    color: #fff;
    font-size: 0.9rem; }
    .sec-todo-list .sort-data.open {
      display: block; }
    .sec-todo-list .sort-data i {
      color: #fff; }
    .sec-todo-list .sort-data .type {
      display: inline-block;
      margin-left: 5px;
      padding: 5px;
      cursor: pointer; }
    .sec-todo-list .sort-data .close {
      display: inline-block;
      float: right;
      padding: 5px;
      cursor: pointer; }
  .sec-todo-list .tasks {
    margin: 15px 30px 0; }
    .sec-todo-list .tasks li {
      padding-right: calc(2.14286em + 10px);
      font-size: 1.1rem;
      border-bottom: 1px solid #eee;
      background: #fff;
      transition: background-color 0.3s; }
      .sec-todo-list .tasks li:hover {
        background: #eee; }
      .sec-todo-list .tasks li.complete span {
        text-decoration: line-through; }
      .sec-todo-list .tasks li.open {
        background: #ddd; }
      .sec-todo-list .tasks li div {
        padding-left: 10px; }
        .sec-todo-list .tasks li div i {
          padding: 22px 0;
          cursor: pointer; }
        .sec-todo-list .tasks li div input, .sec-todo-list .tasks li div span {
          display: inline-block;
          width: 100%;
          padding: 23px 0;
          background: inherit;
          border: none;
          box-shadow: none;
          outline: none; }
        .sec-todo-list .tasks li div span {
          cursor: default; }
      .sec-todo-list .tasks li.add-task {
        color: #ccc; }

/*******  task-info  *******/
.task-info {
  display: none;
  position: absolute;
  right: 0;
  width: 25%;
  height: 100%;
  background: #eee;
  border-left: 1px solid #ddd;
  z-index: 99; }
  .task-info.open {
    display: block; }
  .task-info .header {
    background: #fff;
    border-bottom: 1px solid #ddd; }
    .task-info .header .fa {
      padding: 30px 15px 30px 20px;
      cursor: pointer; }
    .task-info .header input {
      display: inline-block;
      padding: 23px 0;
      background: inherit;
      border: none;
      box-shadow: none;
      outline: none; }
  .task-info .info-item {
    border-radius: 5px;
    background: #fff;
    margin: 15px 5px 0;
    padding: 20px 15px; }
    .task-info .info-item.favorite, .task-info .info-item.trash {
      cursor: pointer; }
    .task-info .info-item i {
      padding-right: 10px; }
  .task-info .footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    color: #666;
    background: #fff;
    border-top: 1px solid #ddd; }
